* {
  margin: 0px;
  padding: 0px;
}

.box {
  width: 600px;
  height: 70px;
  border: 2px solid rgb(140, 47, 57);
  border-radius: 15px;
  padding: 50px;
  margin: 75px;
  background-color: rgb(178, 58, 72);
  position: absolute;
  left: -30px;
  top: 100px;
}

