.logo {
  width: 200px;
  height: 170px;
  position: absolute;
  border-radius: 15px 0px 0px 15px;
  left: 0px;
  top: 0px;
  border-right: 2px solid rgb(140, 47, 57);
}

.text {
  position: absolute;
  left: 33%;
  top: 15%;
  font-family: "", sans-serif;
}

.text-project {
  position: absolute;
  left: 33%;
  top: 38%;
  font-family: "Gill Sans", sans-serif;
}

.text-reward {
  position: absolute;
  left: 33%;
  bottom: 15%;
  font-family: "Gill Sans", sans-serif;
  font-weight: normal;
}

.redeemButton {
  position: absolute;
  right: 10px;
  bottom: 10px;
  background-color: rgb(234, 140, 85);
  border-color: rgb(140, 47, 57);
  border-radius: 10px;
  font-family: "Gill Sans", sans-serif;
  padding: 12px;
  font-weight: bold;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
